	/* # Section Animation
  ================================================== */
	.vc_editor .w-animate { opacity: 1; }
	@media only screen and (min-width: 961px) {
	.w-animate { opacity: 0; }
	.w-start_animation { opacity: 1; -webkit-animation: w-animate-btt 0.5s; animation: w-animate-btt 0.5s; -webkit-backface-visibility:hidden; backface-visibility:hidden; }
	#nav .w-start_animation { opacity: 1; -webkit-animation: w-animate-btt-nav 0.28s; animation: w-animate-btt-nav 0.28s;  -webkit-backface-visibility:hidden; backface-visibility:hidden; }
	}

	/* Bottom to Top Keyframes */
	@-webkit-keyframes w-animate-btt {
	0% { -webkit-transform: translate3d(0, 110px, 0); opacity: 0; }
	100% { -webkit-transform: translate3d(0, 0, 0); opacity: 1; }
	}
	@keyframes w-animate-btt {
	0% { -webkit-transform: translate3d(0, 110px, 0); transform: translate3d(0, 110px, 0); opacity: 0; }
	100% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); opacity: 1; }
	}

	
	/* Bottom to Top Keyframes (mega menu) */
	@-webkit-keyframes w-animate-btt-nav {
	0% { -webkit-transform: translate3d(0, 30px, 0); opacity: 0; }
	100% { -webkit-transform: translate3d(0, 0, 0); opacity: 1; }
	}
	@keyframes w-animate-btt-nav {
	0% { -webkit-transform: translate3d(0, 30px, 0); transform: translate3d(0, 30px, 0); opacity: 0; }
	100% { -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); opacity: 1; }
	}
  
	
