	/* Get the bourbon mixin from http://bourbon.io */
	/* Reset */
	.flip-clock-wrapper * { -ms-box-sizing: border-box; -o-box-sizing: border-box; box-sizing: border-box; -webkit-backface-visibility: hidden; backface-visibility: hidden;} 
	.flip-clock-wrapper a { cursor: pointer; text-decoration: none; color: #ccc;} 
	.flip-clock-wrapper a:hover { color: #fff;} 
	.flip-clock-wrapper ul { list-style: none;}
	.flip-clock-wrapper.clearfix:before, .flip-clock-wrapper.clearfix:after { content: " "; display: table;} 
	.flip-clock-wrapper.clearfix:after { clear: both;} 
	.flip-clock-wrapper.clearfix { *zoom: 1;} 
	/* Main */
	.flip-clock-wrapper { font: normal 11px "Helvetica Neue", Helvetica, sans-serif; -webkit-user-select: none;} 
	.flip-clock-meridium { background: none !important; box-shadow: 0 0 0 !important; font-size: 36px !important;} 
	.flip-clock-meridium a { color: #313333;} 
	.flip-clock-wrapper { text-align: center; position: relative; padding-left:0;} 
	.flip-clock-wrapper:before, .flip-clock-wrapper:after { content: " "; /* 1 */ display: table; /* 2 */ }
	.flip-clock-wrapper:after { clear: both;} 
	/* Skeleton */
	.flip-clock-wrapper ul { position: relative; float: left; margin: 2px; width: 50px; height: 50px; font-size: 80px; font-weight: bold; line-height: 87px; border-radius: 3px; background: rgba(0, 0, 0, 0.21);} 
	.flip-clock-wrapper ul li { z-index: 1; position: absolute; left: 0; top: 0; width: 100%; height: 100%; line-height: 54px; text-decoration: none !important;} 
	.flip-clock-wrapper ul li:first-child { z-index: 2;} 
	.flip-clock-wrapper ul li a { display: block; height: 100%; -webkit-perspective: 200px; perspective: 200px; margin: 0 !important; overflow: visible !important; cursor: default !important;} 
	.flip-clock-wrapper ul li a div { z-index: 1; position: absolute; left: 0; width: 100%; height: 50%; font-size: 80px; overflow: hidden;  outline: 1px solid transparent;} 
	.flip-clock-wrapper ul li a div .shadow { position: absolute; width: 100%; height: 100%; z-index: 2;} 
	.flip-clock-wrapper ul li a div.up { -webkit-transform-origin: 50% 100%; transform-origin: 50% 100%; top: 0;} 
	.flip-clock-wrapper ul li a div.up:after { content: ""; position: absolute; top: 24px; left: 0; z-index: 5; width: 100%; height: 3px; background-color: rgba(0, 0, 0, 0.12);} 
	.flip-clock-wrapper ul li a div.down { -webkit-transform-origin: 50% 0; transform-origin: 50% 0; bottom: 0; border-bottom-left-radius: 3px; border-bottom-right-radius: 3px;} 
	.flip-clock-wrapper ul li a div div.inn { position: absolute; left: 0; z-index: 1; width: 100%; height: 200%; color: #fff; text-shadow: 0 0 2px rgba(0, 0, 0,0.25); text-align: center; background-color: rgb(251, 84, 92); border-radius: 3px; font-size: 48px;} 
	.flip-clock-wrapper ul li a div.up div.inn { top: 0;} 
	.flip-clock-wrapper ul li a div.down div.inn { bottom: 0;} 
	/* PLAY */
	.flip-clock-wrapper ul.play li.flip-clock-before { z-index: 3;} 
	.flip-clock-wrapper .flip { box-shadow: 0 2px 5px rgba(0, 0, 0, 0.17);} 
	.flip-clock-wrapper ul.play li.flip-clock-active { -webkit-animation: asd 0.5s 0.5s linear both; animation: asd 0.5s 0.5s linear both; z-index: 5;} 
	.flip-clock-divider { float: left; display: inline-block; position: relative; width: 18px; height: 62px;} 
	.flip-clock-divider:first-child { width: 0;} 
	.flip-clock-dot { display: none; background: #323434; width: 10px; height: 10px; position: absolute; border-radius: 50%; box-shadow: 0 0 5px rgba(0, 0, 0, 0.5); left: 5px;} 
	.flip-clock-divider .flip-clock-label { position: absolute; bottom: -1.5em; right: -71px; color: #101010; font-weight:bold;  text-shadow: none; text-transform: uppercase;} 
	.blox.dark .flip-clock-divider .flip-clock-label { color:#8a8a8a;}
	.flip-clock-divider.seconds .flip-clock-label { right: -82px;} 
	.flip-clock-dot.top { top: 30px;} 
	.flip-clock-dot.bottom { bottom: 30px;} 
	@-webkit-keyframes asd {
	  0% { z-index: 2;}
	  20% { z-index: 4;}
	  100% { z-index: 4;} }
	@keyframes asd {
	  0% { z-index: 2;}
	  20% { z-index: 4;}
	  100% { z-index: 4;} }
	.flip-clock-wrapper ul.play li.flip-clock-active .down { z-index: 2; -webkit-animation: turn 0.5s 0.5s linear both; animation: turn 0.5s 0.5s linear both;} 
	@-webkit-keyframes turn {
	  0% { -webkit-transform: rotateX(90deg);}
	  100% { -webkit-transform: rotateX(0deg);} }
	@keyframes turn {
	  0% { -webkit-transform: rotateX(90deg); transform: rotateX(90deg);}
	  100% { -webkit-transform: rotateX(0deg); transform: rotateX(0deg);} }
	.flip-clock-wrapper ul.play li.flip-clock-before .up { z-index: 2; -webkit-animation: turn2 0.5s linear both; animation: turn2 0.5s linear both;} 
	@-webkit-keyframes turn2 {
	  0% { -webkit-transform: rotateX(0deg);}
	  100% { -webkit-transform: rotateX(-90deg);} }
	@keyframes turn2 {
	  0% { -webkit-transform: rotateX(0deg); transform: rotateX(0deg);}
	  100% { -webkit-transform: rotateX(-90deg); transform: rotateX(-90deg);} }
	.flip-clock-wrapper ul li.flip-clock-active { z-index: 3;}
	/* SHADOW */
	.flip-clock-wrapper ul.play li.flip-clock-before .up .shadow { background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(64, 64,64,0.68) 100%); background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, 0.1)), color-stop(100%, rgba(64, 64,64,0.68))); background: linear, top, rgba(0, 0, 0, 0.1) 0%, rgba(64, 64,64,0.68) 100%; background: -ms-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(64, 64,64,0.68) 100%); background: linear, to bottom, rgba(0, 0, 0, 0.1) 0%, rgba(64, 64,64,0.68) 100%; -webkit-animation: show 0.5s linear both; animation: show 0.5s linear both;} 
	.flip-clock-wrapper ul.play li.flip-clock-active .up .shadow { background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(64, 64,64,0.68) 100%); background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, 0.1)), color-stop(100%, rgba(64, 64,64,0.68))); background: linear, top, rgba(0, 0, 0, 0.1) 0%, rgba(64, 64,64,0.68) 100%; background: -ms-linear-gradient(top, rgba(0, 0, 0, 0.1) 0%, rgba(64, 64,64,0.68) 100%); background: linear, to bottom, rgba(0, 0, 0, 0.1) 0%, rgba(64, 64,64,0.68) 100%; -webkit-animation: hide 0.5s 0.3s linear both; animation: hide 0.5s 0.3s linear both;} 
	/*DOWN*/
	.flip-clock-wrapper ul.play li.flip-clock-before .down .shadow { background: -moz-linear-gradient(top, rgba(64, 64,64,0.68) 0%, rgba(0, 0, 0, 0.1) 100%); background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(64, 64,64,0.68)), color-stop(100%, rgba(0, 0, 0, 0.1))); background: linear, top, rgba(64, 64,64,0.68) 0%, rgba(0, 0, 0, 0.1) 100%; background: -ms-linear-gradient(top, rgba(64, 64,64,0.68) 0%, rgba(0, 0, 0, 0.1) 100%); background: linear, to bottom, rgba(64, 64,64,0.68) 0%, rgba(0, 0, 0, 0.1) 100%; -webkit-animation: show 0.5s linear both; animation: show 0.5s linear both;} 
	.flip-clock-wrapper ul.play li.flip-clock-active .down .shadow { background: -moz-linear-gradient(top, rgba(64, 64,64,0.68) 0%, rgba(0, 0, 0, 0.1) 100%); background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(64, 64,64,0.68)), color-stop(100%, rgba(0, 0, 0, 0.1))); background: linear, top, rgba(64, 64,64,0.68) 0%, rgba(0, 0, 0, 0.1) 100%; background: -ms-linear-gradient(top, rgba(64, 64,64,0.68) 0%, rgba(0, 0, 0, 0.1) 100%); background: linear, to bottom, rgba(64, 64,64,0.68) 0%, rgba(0, 0, 0, 0.1) 100%; -webkit-animation: hide 0.5s 0.3s linear both; animation: hide 0.5s 0.2s linear both;} 
	@-webkit-keyframes show {
	  0% { opacity: 0;}
	  100% { opacity: 1;} }
	@keyframes show {
	  0% { opacity: 0;}
	  100% { opacity: 1;} }
	@-webkit-keyframes hide {
	  0% { opacity: 1;}
	  100% { opacity: 0;} }
	@keyframes hide {
	  0% { opacity: 1;}
	   100% { opacity: 0;} } 
		





