	.dropup, .dropdown { position: relative;}
	.dropdown-toggle { *margin-bottom: -3px;}
	.dropdown-toggle:active, .open .dropdown-toggle { outline: 0;}
	.caret { display: inline-block; width: 0; height: 0; vertical-align: top; border-top: 4px solid #000; border-right: 4px solid transparent; border-left: 4px solid transparent; content: ''; opacity: 0.3; filter: alpha(opacity=30);}
	.dropdown .caret { margin-top: 8px; margin-left: 2px;}
	.dropdown:hover .caret, .open .caret { opacity: 1; filter: alpha(opacity=100);}
	.dropdown-menu { position: absolute; top: 100%; left: 0; z-index: 1000; display: none; float: left; min-width: 160px; padding: 4px 0; margin: 1px 0 0; list-style: none; background-color: #fff; border: 1px solid #ccc; border: 1px solid rgba(0, 0, 0, 0.2); *border-right-width: 2px; *border-bottom-width: 2px; border-radius: 5px; box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2); -webkit-background-clip: padding-box; -moz-background-clip: padding; background-clip: padding-box;}
	.dropdown-menu.pull-right { right: 0; left: auto;}
	.dropdown-menu .divider { *width: 100%; height: 1px; margin: 8px 1px; *margin: -5px 0 5px; overflow: hidden; background-color: #e5e5e5; border-bottom: 1px solid #fff;}
	.dropdown-menu a { display: block; padding: 3px 15px; clear: both; font-weight: normal; line-height: 18px; color: #7a7f83; white-space: nowrap;}
	.dropdown-menu li > a:hover, .dropdown-menu .active > a, .dropdown-menu .active > a:hover { color: #fff; text-decoration: none; background-color: #1eb1e4;}
	.open { *z-index: 1000;}
	.open > .dropdown-menu { display: block;}
	.pull-right > .dropdown-menu { right: 0; left: auto;}
	 .well, .promobox { min-height: 20px; padding: 19px; margin-bottom: 20px; background-color: #f9f9f9; border: 1px solid #e1e7ec; border: 1px solid rgba(0, 0, 0, 0.05); border-radius: 4px; box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);}
	.well blockquote { border-color: #e5e5e5; border-color: rgba(0, 0, 0, 0.15);}
	.well blockquote:before, blockquote.well:before { content:''; content:none;}
	.well-large { padding: 24px; border-radius: 6px;}
	.well-small { padding: 9px; border-radius: 3px;}
	.promobox { background: #fdfdfd; padding-bottom: 11px;} .promobox button, .promobox .button { float:right;}
	.collapse { position: relative; height: 0; overflow: hidden; -webkit-transition: height 0.35s ease; transition: height 0.35s ease;}

	.collapse.in { height: auto;}
	.close { float: right; font-size: 20px; font-weight: bold; line-height: 18px; color: #000; text-shadow: 0 1px 0 #fff; opacity: 0.2; filter: alpha(opacity=20);}
	.close:hover { color: #000; text-decoration: none; cursor: pointer; opacity: 0.4; filter: alpha(opacity=40);}
